@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
    width : 100%;
    height : 100vh;
    
    background : linear-gradient(-45deg,#0b485b 10%,#f56217 10% 20%,#2196f3 20% 30%,#f44336 30% 40%,#0b485b 40% 50%,#f56217 50% 60%,#2196f3 60% 70%,#f44336 70% 80%);
    background-size: 400% 400%;
    animation: gradient 10s ease infinite;
    font-family: "open sans";
}

#App{
    position: relative;
    
}

#greet{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
}

.display{
    display: none;
}

@keyframes gradient{
    0%{
        background-position: 0% 50%;    
    }
    50%{
        background-position: 100% 50%;    
    }
    100%{
        background-position: 0% 50%;    
    }
}
.home{
     
    top: 50%;
    left: 50%;
    display: block;
    color: white;
    border: black 5px solid;
}

.error-div{
    display: block;
    margin-right: auto;
    margin-left: auto;
    border: black 5px solid;
    text-align: center;
    font-weight: bold;
    color: red;
    font-size: 20px;
}
.home-none{
    display: none;
}

.image-compo{
    text-align: center;
    display: block;
    
}

h1{
    border: black 5px solid;
    
}

@media (max-width: 375px) and  (max-height :812px){

    h1{
        margin-top: 20px;
        max-width: 300px;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .error-div{
        margin-top: 10px;
    }
    .home{
        margin-top: 0px;
        display: block;
    }
    
}

.display-image{
    border: white 5px solid;
    border-radius: 5%;
    border: black 5px solid;
}

@media (max-width : 411px){
    .display-image
    {
        width:300px;
        height: auto;
        
    }
}
#button{
    display: grid;
    margin-top: 10px;
}

button{
    justify-self: center;
}

@media (max-width : 414px){
    #button{
        margin-bottom: 50px;
    }
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
  }
::selection{
    color: #fff;
    background: #664AFF;
  }
  
  .wrapper{
    max-width: 450px;
    text-align: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    border: black 5px solid;
  }
  
  .wrapper .search-input{
    background: #fff;
    width: 100%;
    border-radius: 5px;
    position: relative;
    box-shadow: 0px 1px 5px 3px rgba(0,0,0,0.12);
  }
  
  .search-input input{
    height: 55px;
    width: 100%;
    outline: none;
    border: none;
    border-radius: 5px;
    padding: 0 60px 0 20px;
    font-size: 18px;
    box-shadow: 0px 1px 5px rgba(0,0,0,0.1);
  }
  
  .search-input.active input{
    border-radius: 5px 5px 0 0;
  }
  
  .search-input .autocom-box{
    padding: 0;
    opacity: 0;
    pointer-events: none;
    max-height: 280px;
    overflow-y: auto;
  }
  
  .search-input.active .autocom-box{
    padding: 10px 8px;
    opacity: 1;
    pointer-events: auto;
  }
  
  .autocom-box li{
    list-style: none;
    padding: 8px 12px;
    display: none;
    width: 100%;
    cursor: default;
    border-radius: 3px;
  }
  
  .search-input.active .autocom-box li{
    display: block;
  }
  .autocom-box li:hover{
    background: #efefef;
  }
  
  .search-input .icon{
    position: absolute;
    right: 0px;
    top: 0px;
    height: 55px;
    width: 55px;
    text-align: center;
    line-height: 55px;
    font-size: 20px;
    color: #25242c;
    cursor: pointer;
  }

@media (max-width : 375px ){
  .wrapper{
    
    max-width : 300px;
  }
}
